.services-section {
  padding-top: 90px;
}

.services-title {
  font-size: 28px;
  margin-bottom: 30px;
  @media screen and (min-width: 768px) {
    font-size: 32px;
  }
}

.services-info {
  font-size: 14px;
  margin-bottom: 30px;
  @media screen and (min-width: 768px) {
    font-size: 16px;
  }
}

.services-item {
  box-shadow: rgba(99, 99, 99, 0.4) 0px 2px 8px 0px;
  border-radius: 5px;
  padding: 30px;
  @media screen and (min-width: 1200px) {
    width: 335px;
    height: 250px;
  }
}
.services-item:not(:last-child) {
  margin-bottom: 15px;
  @media screen and (min-width: 768px) {
    margin-bottom: 18px;
  }
}
.services-item-title {
  margin-bottom: 15px;
  font-weight: 800;
  @media screen and (min-width: 768px) {
    font-size: 20px;
  }
}
.services-item-info {
  font-size: 14px;
  font-weight: 400;
  @media screen and (min-width: 768px) {
    font-size: 16px;
  }
}
.services-list {
  @media screen and (min-width: 1200px) {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
  }
}
